import { Behaviour } from 'behaviour.js'

import lerp from 'lerp'

//Client side prediction

export default class SmoothMovement extends Behaviour {

  onAttach () {

    this.object.incoming = {}

  }

  update () {

    let nextPosition = this.object.incoming.position

    if ( nextPosition ) {
      if ( nextPosition.x ) this.object.x = lerp(this.object.x, nextPosition.x, 0.1)
      if ( nextPosition.y ) this.object.y = lerp(this.object.y, nextPosition.y, 0.1)
    }

    //

    let that = this.object

    let dir = { x: 0, y: 0 }

    console.log(that)

    if (that.input[PIXI.keyboard.Key.LEFT]) { dir.x-- }
    if (that.input[PIXI.keyboard.Key.RIGHT]) { dir.x++ }
    if (that.input[PIXI.keyboard.Key.UP]) { dir.y-- }
    if (that.input[PIXI.keyboard.Key.DOWN]) { dir.y++ }

    if (that.hasPlanet()) {

      let
        x = that.planet.position.x - that.position.x,
        y = that.planet.position.y - that.position.y,
        angle = Math.atan2( y, x ),
        force = 5 * (that.radius * that.planet.radius) / Math.pow( Math.hypot(x, y), 2 ),
        vec = { x: Math.cos( angle ) * force, y: Math.sin( angle ) * force }

      that.velocity.x += vec.x * that.acceleration * .5
      that.velocity.y += vec.y * that.acceleration * .5

      //

      let nextPosition = {
        x: that.position.x + (that.velocity.x + Math.cos(angle - Math.PI/2) * dir.x * that.speed) * .5,
        y: that.position.y + (that.velocity.y + Math.sin(angle - Math.PI/2) * dir.x * that.speed) * .5
      }

      //Check collision

      let radius = that.radius + that.planet.radius
      let distance = Math.hypot(nextPosition.x - that.planet.position.x, nextPosition.y - that.planet.position.y)
      if ( distance < radius ) {
        let nextAngle = Math.atan2( nextPosition.y - that.planet.position.y, nextPosition.x - that.planet.position.x )
        nextPosition.x = that.planet.position.x + Math.cos( nextAngle ) * radius
        nextPosition.y = that.planet.position.y + Math.sin( nextAngle ) * radius
        that.velocity.x = 0
        that.velocity.y = 0
        that.isGrounded = true
      } else if ( distance > radius ) {
        that.isGrounded = false
        if ( distance > that.planet.orbitRadius )
          that.setPlanet( null ) //player left planet
      }

      that.position.x = nextPosition.x
      that.position.y = nextPosition.y

    } else {



    }

  }

}



